import React, { useEffect, useState } from 'react'
import { connect } from 'react-redux'
import { Button, Table, Tag, message, Space, Modal } from 'antd'
import { Container, Header, Price, MainTitle } from './style'
import { ModalModule, FormModule } from '../Form'
import { deleteBookAction } from '../../store/store'
import { ExclamationCircleOutlined } from '@ant-design/icons'

function BookList(props) {
	const [content, setContent] = useState([])
	const [bookInfo, setBookInfo] = useState({})
	const [infoVisible, setInfoVisible] = useState(false)
	const { bookDetail, bookList, deleteBook } = props

	useEffect(() => {
		bookList && setContent(bookList)
	}, [bookList, bookDetail])
	function showConfirm(record) {
		Modal.confirm({
			title: 'Confirm',
			icon: <ExclamationCircleOutlined />,
			content: 'Are you sure to delete this book ?',
			okText: 'Delete',
			cancelText: 'Cancel',
			onOk() {
				message.success('Deleted successfully')
				deleteBook(record.key)
			},
			onCancel() {
				return new Promise((resolve) => resolve())
			},
		})
	}

	const columns = [
		{
			title: 'Book',
			dataIndex: 'title',
			key: 'title',
			render: (text, record) => (
				<MainTitle
					onClick={async () => {
						const info = content.filter((item) => item.key === record.key)
						setBookInfo(info[0])
						setInfoVisible(true)
					}}
				>
					{text}
				</MainTitle>
			),
		},
		{
			title: 'Author',
			dataIndex: 'author',
			key: 'author',
		},
		{
			title: 'Category',
			key: 'category',
			dataIndex: 'category',
			render: (category) => {
				const tag = category
				let color = tag.length >= 10 ? 'blue' : 'green'
				return (
					<Tag color={color} key={tag}>
						{tag.toUpperCase()}
					</Tag>
				)
			},
		},

		{
			title: 'Price',
			dataIndex: 'price',
			key: 'price',
			render: (price) => {
				return <Price>${Number(price).toFixed(2)}</Price>
			},
		},
		{
			title: 'Action',
			key: 'action',
			render: (text, record) => (
				<Space size='middle'>
					<Button
						type='danger'
						onClick={() => {
							showConfirm(record)
						}}
					>
						Delete
					</Button>
				</Space>
			),
		},
	]
	return (
		<Container>
			<Header>BookStore</Header>
			<ModalModule></ModalModule>
			<FormModule _visible={infoVisible} _initInfo={bookInfo} _setVisible={setInfoVisible}></FormModule>
			<Table columns={columns} pagination={false} dataSource={content} />
		</Container>
	)
}

const mapStateToProps = (state) => {
	return {
		bookList: state.home.BookList,
		bookDetail: state.home.bookInfo,
	}
}

const mapDispatchToProps = (dispatch) => {
	return {
		deleteBook(index) {
			dispatch(deleteBookAction(index))
		},
	}
}

export default connect(mapStateToProps, mapDispatchToProps)(BookList)
