import React, { Fragment, useEffect, useRef, useState } from 'react'
import { Form, message, Input, Button, InputNumber, Modal } from 'antd'
import { connect } from 'react-redux'
import { processUploadAction, updateBookAction } from '../../store/store'

export const ModalModule = () => {
	const [visible, setVisible] = useState(false)
	const showModal = () => {
		setVisible(true)
	}
	const initInfo = {
		title: '',
		description: '',
		price: 0,
		category: '',
		author: '',
	}
	return (
		<div style={{ float: 'right', marginRight: '9%' }}>
			<Button type='primary' onClick={showModal}>
				Add Book
			</Button>
			<FormModule _visible={visible} _initInfo={initInfo} _setVisible={setVisible}></FormModule>
		</div>
	)
}

export const _FormModule = (props) => {
	const { processUpload, _initInfo, _visible, _setVisible, updateBook } = props
	const [form] = Form.useForm()
	const [bookInfo, setBookInfo] = useState({})
	const formRef = useRef(null)
	const onFinish = (value) => {
		for (let [key, val] of Object.entries(value)) {
			if (typeof val === 'string') {
				val = `${val.trim()}`
			}
		}
		if (bookInfo.key) {
			value['key'] = bookInfo.key
		}
		processUpload(value)
		bookInfo.key ? message.success('Modified Successfully') : message.success('Added Successfully')
		form.resetFields()
		_setVisible(false)
	}
	useEffect(() => {
		if (formRef.current) {
			setBookInfo(_initInfo)
			form.setFieldsValue(_initInfo)
		}
	}, [formRef, _initInfo, _visible])

	const handleCancel = () => {
		form.resetFields()
		_setVisible(false)
	}
	const [componentSize, setComponentSize] = useState('default')
	const onFormLayoutChange = ({ size }) => {
		setComponentSize(size)
	}

	const formItemLayout = {
		labelCol: {
			span: 4,
		},
		wrapperCol: {
			span: 8,
		},
	}

	return (
		<Modal visible={_visible} title='Book Information' onOk={onFinish} onCancel={handleCancel} footer={null}>
			<Fragment>
				<Form
					onFinishFailed={() => {
						message.error('Input error')
						return
					}}
					ref={formRef}
					form={form}
					labelCol={{
						span: 4,
					}}
					wrapperCol={{
						span: 20,
					}}
					onFinish={onFinish}
					layout='horizontal'
					onValuesChange={onFormLayoutChange}
					size={componentSize}
				>
					<Form.Item
						{...formItemLayout}
						name='title'
						label='Book'
						rules={[
							{
								required: true,
								message: 'Please input the book name',
							},
						]}
					>
						<Input placeholder='Input the book name' />
					</Form.Item>
					<Form.Item
						{...formItemLayout}
						name='author'
						label='Author'
						rules={[
							{
								required: true,
								message: 'Please input the author',
							},
						]}
					>
						<Input placeholder='Input the author' />
					</Form.Item>
					<Form.Item
						{...formItemLayout}
						name='category'
						label='Category'
						rules={[
							{
								required: true,
								message: 'Please input the category',
							},
						]}
					>
						<Input placeholder='Input the category' />
					</Form.Item>
					<Form.Item name='price' label='Price' rules={[{ required: true, type: 'number', min: 0, max: 10000 }]}>
						<InputNumber
							precision={2}
							formatter={(value) => `$ ${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ',')}
							parser={(value) => value.replace(/\$\s?|(,*)/g, '')}
							style={{ width: '100px' }}
						/>
					</Form.Item>
					<Form.Item name='description' label='Description' rules={[{ required: false, message: "Please enter book's description " }]}>
						<Input.TextArea placeholder='Input description' autoSize={true} showCount rows={3} maxLength={2000} />
					</Form.Item>
					<Form.Item wrapperCol={{ span: 12, offset: 6 }}>
						<Button style={{ marginLeft: '30px' }} type='primary' htmlType='submit'>
							Submit
						</Button>
					</Form.Item>
				</Form>
			</Fragment>
		</Modal>
	)
}

const mapDispatchToProps = (dispatch) => {
	return {
		processUpload(newBook) {
			dispatch(processUploadAction(newBook))
		},
		updateBook(index) {
			dispatch(updateBookAction(index))
		},
	}
}

export const FormModule = connect(null, mapDispatchToProps)(_FormModule)
