import styled from 'styled-components'

export const Header = styled.h1`
	margin-top: 20px;
	text-align: center;
`
export const Container = styled.div`
	width: 100%;
`
export const Title = styled.span`
	color: #58dcd6;
	font-size: 16px;
	display: inline-block;
	width: 100px;
`
export const Info = styled.span`
	font-size: 16px;
`
export const Price = styled.span`
	font-style: italic;
	color: #36b4e4;
`
export const PriceInline = styled.span`
	font-style: italic;
	color: #36b4e4;
	font-size: 16px;
`
export const NoInfo = styled.span`
	font-size: 16px;
	color: #36b4e4;
	display: inline-block;
`
export const Description = styled.div`
	margin-top: -21px;
	white-space: pre-wrap;
	margin-left: 100px;
	font-size: 15px;
	display: inline-block;
	padding: 5px;
	background-color: rgb(247 247 247);
	border-radius: 10px;
`
export const MainTitle = styled.a`
	font-size: 15px;
`
