import { v4 as uuid } from 'uuid'
import initState from '../data'
import { storage } from '../util'
export const actionTypes = {
	INIT_BOOK_LIST: 'INIT_BOOK_LIST',
	ADD_BOOK: 'ADD_BOOK',
	DELETE_BOOK: 'DELETE_BOOK',
	CHECK_INFO: 'CHECK_INFO',
	UPDATE_BOOK: 'UPDATE_BOOK',
}

export const initBookListAction = () => {
	const newBookList = initState.BookList
	return (dispatch) =>
		dispatch({
			type: actionTypes.INIT_BOOK_LIST,
			data: newBookList,
		})
}
export const getBookInfo = (id) => {
	return (dispatch, getState) => {
		const list = getState.home.BookList
		const info = list.filter((item) => item.key === id)
		console.log(info)
		return dispatch({
			type: actionTypes.CHECK_INFO,
			data: info[0],
		})
	}
}
export const processUploadAction = (info) => {
	if (info.key) {
		return updateBookAction(info)
	}
	return addBookAction(info)
}

const processStorage = (data) => {
	const temp = new Object()
	temp['BookList'] = data
	storage.set('book_storage', temp)
}

export const addBookAction = (info) => {
	return (dispatch, getState) => {
		const list = getState().home.BookList
		const { title, category, description, price, author } = info
		let newBook = {
			key: uuid(),
			title,
			price,
			author,
			category,
			description,
		}
		const newBookList = [...list, newBook]
		processStorage(newBookList)
		return dispatch({
			type: actionTypes.ADD_BOOK,
			data: newBookList,
		})
	}
}
export const updateBookAction = (info) => {
	return (dispatch, getState) => {
		let list = getState().home.BookList
		let newBookList = []
		list.map((item, index) => {
			if (item.key !== info.key) return newBookList.push(item)
			newBookList.push(info)
		})
		processStorage(newBookList)
		return dispatch({
			type: actionTypes.UPDATE_BOOK,
			data: newBookList,
		})
	}
}

export const deleteBookAction = (key) => {
	return (dispatch, getState) => {
		const list = getState().home.BookList
		const newBookList = list.filter((item) => item.key !== key)
		processStorage(newBookList)
		dispatch({
			type: actionTypes.DELETE_BOOK,
			data: newBookList,
		})
	}
}

export const reducer = (state = initState, action) => {
	switch (action.type) {
		case 'ADD_BOOK': {
			const newState = new Object()
			newState.BookList = action.data
			return newState
		}
		case 'UPDATE_BOOK': {
			const newState = new Object()
			newState.BookList = action.data
			return newState
		}

		case 'DELETE_BOOK': {
			const newState = new Object()
			newState.BookList = action.data
			return newState
		}
		case 'INIT_BOOK_LIST': {
			const newState = new Object()
			newState.BookList = action.data
			return newState
		}
		case 'CHECK_INFO': {
			const newState = new Object()
			newState.bookInfo = action.data
			return newState
		}
		default:
			if (!!storage.get('book_storage')) {
				return JSON.parse(storage.get('book_storage'))
			}
			return state
	}
}
