let timer
class _Storage {
	set = (name, content) => {
		if (!name) return
		if (typeof content !== 'string') {
			content = JSON.stringify(content)
		}
		localStorage.setItem(name, content)
	}
	get = (name) => {
		if (!name) return
		return localStorage.getItem(name)
	}
	del = (name) => {
		if (!name) return
		localStorage.removeItem(name)
	}
}

export const storage = new _Storage()

export const Timeout = (func, gap) => {
	timer = setTimeout(() => {
		func()
		clearTimeout(timer)
		Timeout(func, gap)
	}, gap)
}

